body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.side-nav{
	height:100%;
	background:#ccac9d;
	box-shadow:1px 0px 7px rgba(0,0,0,0.5);
	position:fixed;
	top:0;
	left:0;
	width:70%;
	max-width:300px;
	width:300px;
	z-index:200;
	transition: ease-out, -webkit-transform 0.5s;
	transition: transform 0.5s, ease-out;
	transition: transform 0.5s, ease-out, -webkit-transform 0.5s;
}

.hide{
	-webkit-transform:translateX(-100%);
	        transform:translateX(-100%);
}

.nav{
 background:#ccac9d;
}
.footer{
	position:absolute;
	bottom:0;
}
.bar{
	border-radius:10px;
	position:fixed;
	z-index:200;
	background:#ccac9d;
	top:20px;
	left:20px;
	box-shadow:0px 0px 5px rgba(0,0,0,0.5);
}

@media(max-width:769px){
  .nav{
    display:none;
  }
 
}

@media(min-width:770px){
	.bar{
		display:none;
	}
	.side-nav{
		display:none;
	}
	.backdrop{
		display:none;
	}

}



.name-style{
	font-size:80px;
}

.dottedBorder{
  border:none;
  border-top:8px dotted gray;
  color:#fff;
}

.show{
  display:block;
  -webkit-animation-name: showit;
  -webkit-animation-duration: 5s;
}

.link-effect{
  -webkit-animation-name: grow-effect;
    -webkit-animation-duration: 2s;
  transition: all .2s ease-in-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

.link-effect:hover{
   -webkit-animation-name: none;
}

 @-webkit-keyframes grow-effect{
 from {-webkit-transform: scale(.8);transform: scale(.8);}
 50%{-webkit-transform: scale(1);transform: scale(1);}
  to {-webkit-transform: scale(.8);transform: scale(.8);}
}

@-webkit-keyframes showit {
  from {opacity:0;}
  to {opacity:1;}
}


@media(min-width:770px){
	.hidelink{
		display:none;
	}

}

.hover_on_link:hover{
    color:#ccac9d;
}
a:hover, a:active, a:focus {
    outline: none;
    border: 0;
}
@media(max-width:600px){
  .projectImage{
    width:325px;
    height:250px;
  }
 
}
@media(max-width:376px){
  .projectImage{
    width:325px;
    height:230px;
  }
 
}


input:focus {
    outline:none;
}

textarea:focus {
    outline:none;
}

.App {
  text-align: center;
}

.scrollbar{
    overflow-y:scroll;
    overflow-x:hidden;
}

#style-1::-webkit-scrollbar {
    width: 0px;
} 

#style-1::-webkit-scrollbar-thumb {
    background-color: #ccac9d;
}

.icon i {
 font-size:5rem;
 margin:20px;
 padding:10px;
}

.backdrop {
	position:fixed;
	width:100%;
	height:100%;
	top:0;
	left:0;
	background:rgba(0,0,0,0.3);
	z-index:100;
}

.title_and_link{
  border:solid #ccac9d 1px;
  border-radius:10px;
/*border-top-left-radius: 100px;
  border-bottom-right-radius: 100px;*/
}

.shadowE{
  box-shadow:0px 15px 10px -15px rgba(0,0,0,0.5);
}


@media(max-width:769px){
  .space{
    display:none;
  }
  .icon i{
   font-size:3rem;
   margin:20px;
   padding:10px;
  }
